.footer {
  background: #5a98d0;
  color: #ffffff;
}

.footer a {
  color: white;
  border-color: transparent;
}

.footer a:hover {
  color: rgba(255, 255, 255, 0.4);
}

.footer::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  right: 7%;
  bottom: 0;
  left: 0;
  background: #316099;
}

.footer__inner {
  display: flex;
  flex-flow: row nowrap;
  position: relative;
  z-index: 2;
  width: 860px;
  padding: 95px 0 0;
}

.footer__inner::before {
  content: "";
  position: absolute;
  opacity: 0.4;
  z-index: 1;
  top: 0;
  right: 0;
  left: -100vw;
  background: #ffffff;
  height: 1px;
}

.footer__inner-2 {
  display: flex;
  flex-flow: row nowrap;
}

.footer__inner-2 .footer__list {
  margin-right: 75px;
  width: 230px;
}

.footer__inner-2 .footer__list:last-of-type {
  margin-right: 0;
}

.footer__inner_bottom {
  padding: 64px 0;
  color: rgba(255, 255, 255, 0.4);
}

.footer__inner_bottom::before {
  display: none;
}

.footer__list {
  display: flex;
  flex-flow: column nowrap;
  margin-right: 100px;
  width: 300px;
}

.footer__item {
  display: flex;
  flex-flow: row nowrap;
  margin-bottom: 21px;
}

.footer__item:last-of-type {
  margin-bottom: 0;
}

.footer__item div {
  width: 19px;
  margin-right: 16px;
}

.footer__item-2 {
  margin-bottom: 16px;
}

.footer__item-2:last-of-type {
  margin-bottom: 0;
}

.footer__item-2_title {
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 32px;
}

.footer__item_logo {
  margin-bottom: 32px;
}

.footer__item_logo .logo {
  position: relative;
  padding-left: 63px;
  font-size: 28px;
  line-height: 46px;
  letter-spacing: 0.033em;
}

.footer__item_logo .logo img {
  content: "";
  position: absolute;
  top: 55%;
  left: 0;
  transform: translate3d(0, -50%, 0);
}

@media only screen and (max-width: 991px) {
  .footer__inner {
    flex-flow: column nowrap;
    width: 100%;
  }

  .footer__inner-2 .footer__list {
    width: 200px;
  }

  .footer__list {
    margin-right: 0;
    width: 100%;
    margin-bottom: 100px;
  }
}